import closeIcon from "assets/img/Dialog/closeIcon_Chips.svg";
import InputError from "components/InputError/InputError";
import Label from "components/Label/Label";
import Text from "components/Text/Text";
import { withAuth } from "components/hoc/withAuth";
import { IBaseInputCommonProps } from "components/interface/Input";
import { Chips, ChipsProps } from "primereact/chips";
import React, { forwardRef, useImperativeHandle, useState } from "react";
import { useTranslation } from "react-i18next";

export interface IBaseChipsProps extends ChipsProps, IBaseInputCommonProps {
  setParentError?: any;
  setHasError?: any;
  ref?: any;
  label?: string;
  disabled?: boolean;
}

const BaseChips: React.FC<IBaseChipsProps> = forwardRef((props: any, ref: any) => {
  const {
    hideLabel,
    hideErrorRow,
    label,
    validation,
    onBlur,
    labelClassNames,
    labelStyleObj,
    errorClassNames,
    setParentError,
    setHasError,
    disabled,
    isDisabledByAcl,
    isIgnoreAccessLevel,
    ...primeReactProps
  } = props;

  useImperativeHandle(ref, () => ({
    setChipsErrorMessage: setErrorMessage,
  }));

  const { className, value, style, onChange, name, args, placeholder, onFocus } = primeReactProps;

  const [errorMessage, setErrorMessage] = useState("");
  const { t } = useTranslation();
  const translatedLabel = t(placeholder, args);

  const handleChipRemove = (chipToRemove: any) => {
    const updatedChips = value.filter((chip: any) => chip !== chipToRemove);
    onChange({
      ...primeReactProps,
      value: updatedChips,
    });
  };

  function isValidEmail(email: string) {
    // Regular expression for email validation
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  }
  // ... (previous code)

  const validateChipInputValue = (inputValue: string) => {
    if (!isValidEmail(inputValue)) {
      setErrorMessage("Please enter a valid email");
      setHasError?.(true);
      return false;
    } else if (value.includes(inputValue)) {
      setErrorMessage("Email address already exists");
      setHasError?.(true);
      return false;
    }

    return true;
  };

  const handleKeyDown = (event: any) => {
    if (event.key === " " || event.key === "," || event.key === "Enter" || event.key === "Tab") {
      event.preventDefault();
      const inputValue = event.target.value.trim();

      if (inputValue) {
        if (!validateChipInputValue(inputValue)) {
          return;
        }

        setErrorMessage("");
        setHasError?.(false);
        const updatedChips = [...value, inputValue];
        onChange({
          ...primeReactProps,
          value: updatedChips,
        });
        event.target.value = "";
      }
    } else {
      setParentError?.("");
      setErrorMessage("");
      setHasError?.(false);
    }
  };

  const chipTemplate = (chip: any) => {
    return (
      <div className="mx-1 my-0.5 flex bg-secondary-steelBlue-300 pl-2 pr-1 rounded-sm gap-0.5">
        <Text label={chip} className=" text-primary-ptext-1000" />
        <img
          className="w-5 h-5 cursor-pointer"
          src={closeIcon}
          alt="closeIcon"
          onClick={() => handleChipRemove(chip)}
          onKeyDown={(e) => {
            // Handle keyboard events (e.g., 'Enter' key) to trigger the same action as the click
            if (e.key === "Enter") {
              handleChipRemove(chip);
            }
          }}
          tabIndex={0} // This makes the image focusable and interactive for keyboard users
        />
      </div>
    );
  };

  const onBlurChips = (event: any) => {
    event.preventDefault();
    const inputValue = event.target.value.trim();
    if (inputValue) {
      if (!validateChipInputValue(inputValue)) {
        return;
      }

      setErrorMessage("");
      setHasError?.(false);
      const updatedChips = [...value, inputValue];
      onChange({
        ...primeReactProps,
        value: updatedChips,
      });
      event.target.value = "";
    }

    onBlur?.();
  };

  return (
    <div className="group grid">
      {!hideLabel && (
        <Label
          label={label}
          labelClassNames={`${labelClassNames} mb-1 `}
          labelStyleObj={labelStyleObj}
          isInvalid={validation}
        />
      )}

      <Chips
        {...primeReactProps}
        value={value}
        max={10}
        onChange={onChange}
        onKeyDown={handleKeyDown}
        name={name}
        placeholder={translatedLabel}
        removeIcon="pi pi-times ml-2.5 hidden"
        separator=", "
        itemTemplate={chipTemplate}
        onBlur={onBlurChips}
        disabled={disabled || isDisabledByAcl}
        className={`${
          className ?? ""
        } cc-chipsbox border hover:border-primary-sapphireBlue-600 pl-2 py-2 rounded-md text-xs text-primary-ptext-1000 ${
          errorMessage ? "text-semantic-crimson-600 " : ""
        } ${
          isDisabledByAcl
            ? "!bg-secondary-disabledBGColor border-primary-gray-300 placeholder-secondary-gray-500"
            : " "
        }`}
      />

      <div className="text-semantic-crimson-600 error-icon h-3.5">
        {!hideErrorRow ? (
          <InputError
            validation={errorMessage}
            hideErrorRow={hideErrorRow}
            errorClassNames={errorClassNames}
          />
        ) : null}
      </div>
    </div>
  );
});

export default withAuth(BaseChips);
